import { useEffect, useRef, useState } from 'react'
import { buildStaticMapUrl } from '../utils/buildStaticMapUrl'

/**
 * StaticMap Component - Renders Ola Maps static image
 * 
 * Props:
 * - lat (number): Center latitude
 * - lon (number): Center longitude
 * - zoom (number): Zoom level (default 14)
 * - width (number): Image width in pixels (default 800)
 * - height (number): Image height in pixels (default 600)
 * - markers (array): Array of {lat, lon} for markers
 * - path (array): Array of {lat, lon} for route path
 * - className (string): Additional CSS classes
 * - showLoader (boolean): Show loading state (default true)
 */
export default function StaticMap({
  lat,
  lon,
  zoom = 14,
  width = 800,
  height = 600,
  markers = [],
  path = [],
  className = '',
  showLoader = true,
}) {
  const containerId = useRef(`static-map-${Math.random().toString(36).slice(2)}`)
  const [loading, setLoading] = useState(true)
  const [error, setError] = useState(null)
  const [imageUrl, setImageUrl] = useState('')

  useEffect(() => {
    if (lat === undefined || lon === undefined) {
      setError('Missing required props: lat and lon')
      setLoading(false)
      return
    }

    const apiKey = process.env.NEXT_PUBLIC_OLA_MAPS_API_KEY
    if (!apiKey) {
      setError('API key not configured')
      setLoading(false)
      return
    }

    try {
      // Build the static map URL
      const url = buildStaticMapUrl({
        lat,
        lon,
        zoom,
        width,
        height,
        markers,
        path,
        apiKey,
      })

      if (!url) {
        setError('Failed to build map URL')
        setLoading(false)
        return
      }

      setImageUrl(url)
      setLoading(false)
    } catch (err) {
      console.error('StaticMap error:', err)
      setError(`Error: ${err.message}`)
      setLoading(false)
    }
  }, [lat, lon, zoom, width, height, markers, path])

  return (
    <div
      id={containerId.current}
      className={`static-map ${className}`}
      style={{
        position: 'relative',
        width: '100%',
        maxWidth: `${width}px`,
        overflow: 'hidden',
      }}
    >
      {loading && showLoader && (
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            width: '100%',
            height: `${height}px`,
            backgroundColor: '#f0f0f0',
            borderRadius: '8px',
            fontSize: '14px',
            color: '#666',
          }}
        >
          Loading map...
        </div>
      )}

      {error && (
        <div
          style={{
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
            width: '100%',
            height: `${height}px`,
            backgroundColor: '#ffe6e6',
            borderRadius: '8px',
            fontSize: '12px',
            color: '#cc0000',
            padding: '12px',
            textAlign: 'center',
          }}
        >
          {error}
        </div>
      )}

      {!error && imageUrl && (
        <img
          src={imageUrl}
          alt="Static map"
          style={{
            width: '100%',
            height: 'auto',
            display: 'block',
            borderRadius: '8px',
          }}
          onLoad={() => {
            setLoading(false)
          }}
          onError={() => {
            setError('Failed to load map image')
          }}
        />
      )}
    </div>
  )
}
